import React from 'react'
import CardItem from './CardItem'
import './CSS/Cards.css'

function Cards() {
    return (
        <div className='cards'>
            <h1>Una muestra de nuestro trabajo</h1>
            <div className="cards__container">
                <div className="cards__wrapper">
                    <ul className="cards__items">
                        <CardItem 
                        src = {require('./images/img-1.jpg').default}
                        text = "Descripcion del trabajo, es temporal se puede cambiar"
                        label= "Trabajo"
                        path='/contacts'
                        />
                        <CardItem 
                        src = {require('./images/img-2.jpg').default}
                        text = "Descripcion del trabajo, es temporal se puede cambiar"
                        label= "Trabajo"
                        path='/contacts'
                        />
                    </ul>
                    <ul className="cards__items">
                        <CardItem 
                        src = {require('./images/img-3.jpg').default}
                        text = "Descripcion del trabajo, es temporal se puede cambiar"
                        label= "Trabajo"
                        path='/contacts'
                        />
                        <CardItem 
                        src = {require('./images/img-4.jpg').default}
                        text = "Descripcion del trabajo, es temporal se puede cambiar"
                        label= "Trabajo"
                        path='/contacts'
                        />
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Cards
