import React from 'react'
import { Button } from './Button'
import './CSS/Footer.css'
import {Link} from 'react-router-dom'

function Footer() {
    return (
        <div className="footer-container">
            <section className="footer-subscription">
                <p className="footer-subscription-heading">
                    Comunicate con nosotros
                </p>
                <div className="input-areas">
                    <form>
                        <input type="email" name="email" placeholder="Ingresa tu email" className="footer-input"></input>
                        <input type="text" name="text" placeholder="Escribe tus dudas" className="footer-input"></input>
                        <Button buttonStyle='btn--outline'>Enviar</Button>
                    </form>
                </div>
            </section>
            <div className="footer-links">
                <div className="footer-link-wrapper">
                    <div className="footer-link-items">
                        <h2>Nuestro trabajo</h2>
                        <Link to="/services">Servicios</Link>
                        <Link to="/contacts">Contactanos</Link>
                    </div>
                </div>
                <div className="footer-link-wrapper">
                    <div className="footer-link-items">
                        <h2>Nuestro trabajo</h2>
                        <Link to="/services">Servicios</Link>
                        <Link to="/contacts">Contactanos</Link>
                    </div>
                </div>
         
                
            </div>
            
            <section className="social-media">
                <div className="social-media-wrap">
                    <div className="footer-logo">
                        <Link to="/" className="social-logo">
                            PCP <i class="fas fa-water"></i>
                        </Link>
                    </div>
                    <small className="website-rights">PCP 2021</small>
                    <div className="social-icons">
                        <Link className="social-icon-link facebook"
                        to="/"
                        target='_blank'
                        aria-label='Facebook'
                        >
                            <i class="fab fa-facebook-f"></i></Link>
                        <Link className="social-icon-link instagram"
                        to="/"
                        target='_blank'
                        aria-label='Instagram'
                        >
                            <i class="fab fa-instagram"></i></Link>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Footer
